import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Quest from './views/Quest.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/quest',
      name: 'quest',
      component: Quest
    }
  ]
})